import React, { Component } from "react";

import Table from "../../src";
import Pagination from "bee-pagination";

const columns8 = [
  { title: "", dataIndex: "a", key: "a", width: 100 },
  { id: "123", title: "", dataIndex: "b", key: "b", width: 100 },
  { title: "", dataIndex: "c", key: "c", width: 200 },
  {
    title: "",
    dataIndex: "d",
    key: "d"
  }
];

const pageData = {
  1: [
    { a: "", b: "", c: 30, d: "", key: "2" },
    { a: "", b: "", c: 41, d: "", key: "1" },
    { a: "", b: "", c: 25, d: "", key: "3" }
  ],
  2: [
    { a: "", b: "", c: 23, d: "", key: "1" },
    { a: "", b: "", c: 23, d: "", key: "2" }
  ]
};

class Demo8 extends Component {
  constructor(props) {
    super(props);
    this.state = {
      data: pageData[1],
      activePage: 1
    };
  }

  handleSelect(eventKey) {
    this.setState({
      data: pageData[eventKey],
      activePage: eventKey
    });
  }

  render() {
    return (
      <div>
        <Table columns={columns8} data={this.state.data} />
        <Pagination
          first
          last
          prev
          next
          maxButtons={5}
          boundaryLinks
          activePage={this.state.activePage}
          onSelect={this.handleSelect.bind(this)}
          onDataNumSelect={this.dataNumSelect}
          showJump={true}
          total={100}
          dataNum={2}
        />
      </div>
    );
  }
}
export default Demo8;



