import React, { Component } from 'react';
import Table from '../../src';
import Icon from "bee-icon";
import sort from "../../src/lib/sort.js";
let ComplexTable = sort(Table, Icon);
const columns11 = [
  {
    title: "",
    dataIndex: "a",
    key: "a",
    width: 100
  },
  {
    title: "",
    dataIndex: "b",
    key: "b",
    width: 100
  },
  {
    title: "",
    dataIndex: "c",
    key: "c",
    width: 200,
    sorter: (a, b) => a.c - b.c
  },
  {
    title: "",
    dataIndex: "d",
    key: "d"
  },
  {
    title: "",
    dataIndex: "e",
    key: "e",
    sorter: (a, b) => a.c - b.c
  },
];

const data11 = [
  { a: "", b: "", c: 30,d:'', e:139,key: "2" },
  { a: "", b: "", c: 41,d:'', e:109, key: "1" },
  { a: "", b: "", c: 25,d:'', e:159, key: "3" }
];

const defaultProps = {
  prefixCls: "bee-table"
};
class Demo28 extends Component {
  constructor(props) {
    super(props);
    this.state = {
      sortOrder: "",
      data: data11
    };
  }
  /**
   * 
   */
  sortFun = (sortParam)=>{
    console.info(sortParam);
    //
  }
  render() {
    let sortObj = {
      mode:'multiple',
      backSource:true,
      sortFun:this.sortFun
    }
    return <ComplexTable columns={columns11} data={this.state.data} sort={sortObj}/>;
  }
}
Demo28.defaultProps = defaultProps;


export default Demo28;


