import React, { Component } from "react";
import Table from "../../src";

const columns7 = [
  { title: "", dataIndex: "a", key: "a" },
  { title: "", dataIndex: "b", key: "b" },
  { title: "", dataIndex: "c", key: "c" },
  {
    title: "",
    dataIndex: "d",
    key: "d"
  }
];

const data7 = [
  { a: "02", b: "2", c: "", d: "", key: "1" },
  { a: "03", b: "3", c: "", d: "", key: "2" },
  { a: "05", b: "1", c: "", d: "", key: "3" }
];

const columns7_1 = [
  { title: "", dataIndex: "a", key: "a" },
  { title: "", dataIndex: "b", key: "b" },
  { title: "", dataIndex: "c", key: "c" }
];

class Demo7 extends Component {
  constructor(props) {
    super(props);
    this.state = {
      children_data: []
    };
  }

  rowclick = (record, index) => {
    if (record.a === "02") {
      this.setState({
        children_data: [
          { a: "", b: "02", c: "", key: "1" },
          { a: "", b: "02", c: "", key: "2" }
        ]
      });
    } else if (record.a === "03") {
      this.setState({
        children_data: [
          { a: "", b: "03", c: "", key: "1" },
          { a: "", b: "03", c: "", key: "2" },
          { a: "", b: "03", c: "", key: "3" }
        ]
      });
    } else if (record.a === "05") {
      this.setState({
        children_data: [{ a: "", b: "05", c: "", key: "1" }]
      });
    }
  };

  render() {
    return (
      <div>
        <Table
          columns={columns7}
          data={data7}
          onRowClick={this.rowclick}
          title={currentData => <div>: </div>}
        />
        <Table
          style={{ marginTop: 40 }}
          columns={columns7_1}
          data={this.state.children_data}
          title={currentData => <div>: </div>}
        />
      </div>
    );
  }
}

export default Demo7;



