import React, { Component } from "react";
import Table from "../../src";
import Checkbox from "bee-checkbox";
import Button from "bee-button";
import Icon from "bee-icon";
import multiSelect from "../../src/lib/multiSelect.js";
import sort from "../../src/lib/sort.js";
import sum from "../../src/lib/sum.js";

const columns13 = [
  {
    title: "",
    dataIndex: "a",
    key: "a",
    className:'dfasd',
    width: 200
  },
  {
    title: "",
    dataIndex: "b",
    key: "b",
    width: 200,
    sumCol: true,
    sorter: (a, b) => a.c - b.c,
    sorterClick:(data,type)=>{//
      //type value is up or down
      console.log("data",data);
    }
  },
  {
    title: "",
    dataIndex: "c",
    key: "c",
    width: 200,
    sumCol: true,
    sorter: (a, b) => a.c - b.c,
    sorterClick:(data,type)=>{//
      //type value is up or down
      console.log("data",data);
    }
  },
  {
    title: "",
    dataIndex: "e",
    key: "e",
    width: 200,
    sumCol: true,
    sorter: (a, b) => a.c - b.c,
  },
  {
    title: "",
    dataIndex: "d",
    key: "d",
    width: 200
  }
];

const data13 = [
  { a: "", b: 675, c: 30, d: "",e:100, key: "2" },
  { a: "", b: 43, c: 41, d: "",e:90, key: "1" },
  { a: "1", b: 43, c: 81, d: "", e:120,key: "4" },
  { a: "2", b: 43, c: 81, d: "", e:130,key: "5" },
  { a: "", b: 153, c: 25, d: "",e:90, key: "3" }
];

//tablerenderstate
let ComplexTable = multiSelect(sum(sort(Table, Icon)), Checkbox);

class Demo13 extends Component {
  constructor(props) {
    super(props);
    this.state = {
      data13: data13,
      selectedRow: this.selectedRow,
      selectDisabled: this.selectDisabled
    };
  }
  getSelectedDataFunc = data => {
    console.log(data);
  };
  selectDisabled = (record, index) => {
    // console.log(record);
    if (index === 1) {
      return true;
    }
    return false;
  };
  selectedRow = (record, index) => {
    // console.log(record);
    if (index === 0) {
      return true;
    }
    return false;
  };
  onClick = () => {
    this.setState({
      selectedRow: function() {}
    });
  };

  render() {
    let multiObj = {
      type: "checkbox"
    };
    let sortObj = {
      mode:'multiple'
    }
   
    return (
      <div>
        <Button className="editable-add-btn" onClick={this.onClick}>
          change selectedRow
        </Button>
        <ComplexTable
          selectDisabled={this.state.selectDisabled}
          selectedRow={this.state.selectedRow}
          columns={columns13}
          data={this.state.data13}
          multiSelect={multiObj}
          sort={sortObj}
          getSelectedDataFunc={this.getSelectedDataFunc}
        />
      </div>
    );
  }
}
export default Demo13;



