import React, { Component } from "react";
import Button from "bee-button";
import Tooltip from "bee-tooltip";
import Table from "../../src";

const columns = [
  { title: "", dataIndex: "a", key: "a", width:80 , className:"rowClassName"},
  { id: "123", title: "", dataIndex: "b", key: "b", width: 100 },
  { title: "", dataIndex: "c", key: "c", width: 200 },
];

const data = [
  { a: "", b: "", c: 41, key: "1" },
  { a: "", b: "", c: 67, key: "2" },
  { a: "", b: "", c: 25, key: "3" }
];

class Demo26 extends Component {

  constructor(props){
      super(props);
      this.state = {
        data: data,
        selectedRowIndex: 0
      }
  }

  render() {
    return (
      <Table
        columns={columns}
        data={data}
        rowClassName={(record,index,indent)=>{
          if (this.state.selectedRowIndex == index) {
              return 'selected';
          } else {
              return '';
          }
        }}
        onRowClick={(record,index,indent)=>{
          this.setState({ 
              selectedRowIndex: index
          });
        }}
        title={currentData => <div>: </div>}
        footer={currentData => <div>: </div>}
      /> 
    );
  }
}

export default Demo26;



