import React, { Component } from "react";
import Table from "../../src";

const columns16 = [
  {
    title: "",
    dataIndex: "d",
    key: "d", 
    width:200,
    render(text, record, index) {
      return (
        <a
          href="#"
          onClick={() => {
            alert("" + index + ":" + text);
          }}
        >
          
        </a>
      );
    }
  },
  { title: "", dataIndex: "a", key: "a", width: 250 },
  { id: "123", title: "", dataIndex: "b", key: "b", width: 100 },
  { title: "", dataIndex: "c", key: "c", width: 200 },
  
];
const columns17 = [
  {
    title: "",
    dataIndex: "d",
    key: "d",
    width:200,
    render(text, record, index) {
      return (
        <a
          href="#"
          onClick={() => {
            alert("" + index + ":" + text);
          }}
        >
          
        </a>
      );
    }
  },
  { title: "", dataIndex: "a", key: "a", width: 100 },
  { id: "123", title: "", dataIndex: "b", key: "b", width: 100 },
  { title: "", dataIndex: "c", key: "c", width: 200 },
  
];

const data16 = [
  { a: "", b: "", c: 41, d: "", key: "1" },
  { a: "", b: "", c: 67, d: "", key: "2" },
  { a: "", b: "", c: 25, d: "", key: "3" }
];


class Demo16 extends Component {
  constructor(props){
    super(props);
    this.state={
      data_obj:{}
    }
  }
  expandedRowRender = (record, index, indent) => {
    let height = 42 * (this.state.data_obj[record.key].length+ 2);
    
    return (
      <Table
        columns={columns17}
        style={{height:height}}
        data={this.state.data_obj[record.key]} 

      />
    );
  };
  getData=(expanded, record)=>{
    //
    let new_obj = Object.assign({},this.state.data_obj);
    if(expanded){
      if(record.key==='1'){
        new_obj[record.key] = [
          { a: "", b: "", c: 41, d: "", key: "1" },
          { a: "", b: "", c: 67, d: "", key: "2" }
        ]
        this.setState({
          data_obj:new_obj
        })
      }else{
        new_obj[record.key] = [
          { a: "", b: "", c: 41, d: "", key: "1" }
        ]
        this.setState({
          data_obj:new_obj
        })
      }
    }
  }
  haveExpandIcon=(record, index)=>{
    //iconexpandedRowRender
    if(index == 0){
      return true;
    }
    return false;
  }
  render() {
    return (
      <Table
        columns={columns16}
        data={data16}
        onExpand={this.getData}
        expandedRowRender={this.expandedRowRender}
        scroll={{x:true}}
        title={currentData => <div>: </div>}
        footer={currentData => <div>: </div>}
      />
    );
  }
}

export default Demo16;



