import React, { Component } from 'react';
import PropTypes from 'prop-types';
import zhCN from "rc-calendar/lib/locale/zh_CN";
import FormControl from 'bee-form-control';
import Select from 'bee-select';
import InputNumber from 'bee-input-number';
import DatePicker from 'bee-datepicker';
import FilterDropDown from './FilterDropDown';

const { RangePicker } = DatePicker;

const propTypes = {
    filterDropdown: PropTypes.string
};

class FilterType extends Component {
    constructor(props) {
        super(props);
        this.state = {
            value: "",
            text: "",
            selectValue: "",
            dateValue: "",
            open: false,
            condition: props.filterDropdownType == 'string' ? 'LIKE' : 'EQ',
            number: 0
        }
    }

    /**
     * 
     *
     */
    clearFilter = () => {
        let { onFilterClear, dataIndex } = this.props;
        this.setState({
            value: "",//
            condition: this.props.filterDropdownType == 'string' ? 'LIKE' : 'EQ'//
        }, () => {
            //field
            onFilterClear && onFilterClear(dataIndex);
        });
    }

    /**
     * 
     *
     */
    changeText = (val) => {
        this.setState({
            value: val
        });
    }

    /**
     * 
     *
     */
    changeTextCall = (e) => {
        let { onFilterChange, dataIndex } = this.props;
        if (e.keyCode == 13) {
            onFilterChange(dataIndex, e.target.value, this.state.condition);
        }
    }
    /**
     * 
     *
     */
    changeValue = () => {
        this.setState({
            value: ""
        });
    }
    /**
     * 
     *
     * @param {*} key 
     * @param {*} value 1,2,3...6
     */
    onSelectDropdown = (item) => {
        let { onFilterChange, dataIndex } = this.props;
        this.setState({
            condition: item.key
        }, () => {
            onFilterChange && onFilterChange(dataIndex, this.state.value, this.state.condition);
        });
    }

    /**
     * 
     *
     */
    changeNumber = (value) => {
        let { onFilterChange, dataIndex } = this.props;
        this.setState({
            value
        }, () => {
            onFilterChange(dataIndex, value, this.state.condition);
        });
    }
    //
    clearNumber = () => {
        let { onChange } = this.props;
        onChange && onChange("");
        this.setState({
            value: ""
        });
    }

    //
    changeTextCallBlur = (val) => {
        let { onChange } = this.props;
        onChange && onChange(val);
    }
    //
    changeSelect = (value) => {
        let { onFilterChange, dataIndex } = this.props;
        if (onFilterChange) {
            onFilterChange(dataIndex, value, this.state.condition);
            this.setState({
                value
            });
        }
    }
    //
    clearSelectValue = () => {
        this.setState({
            selectValue: ""
        }, () => {
            this.changeSelect("");
        });
    }
    //
    clearDateValue = () => {
        this.setState({
            dateValue: ""
        }, () => {
            this.changeDate("");
        });
    }
    //
    changeDate = (value) => {
        let { onFilterChange, dataIndex } = this.props;
        if (onFilterChange) {
            onFilterChange(dataIndex, value, this.state.condition);
            this.setState({
                value,
                open: false
            });
        }
    }
    //
    /**
     * 
     *
     * @param {*} rendertype ['text','dropdown','date','daterange','number']
     * @returns
     */
    renderControl = (rendertype) => {
        let { dataIndex, filterDropdown, filterDropdownType, format, className, onChange, onSelectDropdown, clsPrefix, locale } = this.props;
        switch (rendertype) {
            case 'text':
                return <div className={`${clsPrefix} filter-wrap`}>
                    <FormControl
                        value={this.state.value}
                        className={className}
                        onChange={this.changeText}
                        onKeyDown={this.changeTextCall}
                    //onBlur={this.changeTextCallBlur}
                    />
                    <FilterDropDown
                        locale={locale}
                        dataIndex={dataIndex}
                        dataText={this.state.value}
                        onSelectDropdown={this.onSelectDropdown}
                        onClickClear={this.clearFilter}
                        isShowClear={this.state.value}
                        isShowCondition={filterDropdown}
                        filterDropdownType={filterDropdownType}
                    >
                    </FilterDropDown>
                </div>
            case 'number':
                return <div className={`${clsPrefix} filter-wrap`}>
                    <InputNumber
                        className={className}
                        value={this.state.value}
                        onChange={this.changeNumber}
                        iconStyle="one"
                    />
                    <FilterDropDown
                        locale={locale}
                        dataIndex={dataIndex}
                        dataText={this.state.value}
                        onSelectDropdown={this.onSelectDropdown}
                        onClickClear={this.clearFilter}
                        isShowClear={this.state.value != 0}
                        isShowCondition={filterDropdown}
                        filterDropdownType={filterDropdownType}
                    >
                    </FilterDropDown>
                </div>
            case 'dropdown':
                return <div className={`${clsPrefix} filter-wrap`}>
                    <Select
                        {...this.props}
                        value={this.state.value}
                        onChange={this.changeSelect}
                    /><FilterDropDown
                        locale={locale}
                        dataIndex={dataIndex}
                        dataText={this.state.value}
                        onSelectDropdown={this.onSelectDropdown}
                        onClickClear={this.clearFilter}
                        isShowCondition={filterDropdown}
                        isShowClear={this.state.value}
                        filterDropdownType={filterDropdownType}
                    >
                    </FilterDropDown></div>
            case 'date':
                return <div className={`${clsPrefix} filter-wrap`}>
                    <DatePicker
                        {...this.props}
                        value={this.state.value}
                        onChange={this.changeDate}
                        open={this.state.open}
                        format={format}
                        locale={zhCN}
                    /><FilterDropDown
                        locale={locale}
                        dataIndex={dataIndex}
                        dataText={this.state.value}
                        onSelectDropdown={this.onSelectDropdown}
                        onClickClear={this.clearFilter}
                        isShowCondition={filterDropdown}
                        isShowClear={this.state.value}
                        filterDropdownType={filterDropdownType}
                    >
                    </FilterDropDown>
                </div>
            case 'daterange':
                return <div className={`${clsPrefix} filter-wrap`}>
                    <RangePicker
                        {...this.props}
                        value={this.state.value}
                        onChange={this.changeDate}
                        open={this.state.open}
                        format={format}
                        showTime={true}
                        locale={zhCN}
                        placeholder={' ~ '}
                        dateInputPlaceholder={['', '']}
                        showClear={true}
                    /><FilterDropDown
                        locale={locale}
                        dataIndex={dataIndex}
                        dataText={this.state.value}
                        onSelectDropdown={this.onSelectDropdown}
                        onClickClear={this.clearFilter}
                        isShowCondition={filterDropdown}
                        isShowClear={this.state.value}
                    >
                    </FilterDropDown>
                </div>
            case 'bool':
                return <div className={`${clsPrefix} filter-wrap`}>
                    <Switch
                        className={className}
                        onChange={onChange}
                    />
                    <FilterDropDown locale={locale}
                        onSelectDropdown={onSelectDropdown}
                    >
                    </FilterDropDown>
                </div>
            default:
                return <div></div>;
        }

    }
    render() {
        let { rendertype } = this.props;
        return (
            this.renderControl(rendertype)
        );
    }
}
FilterType.propTypes = propTypes;
FilterType.defaultProps = {
    filterDropdown: 'show'
}
export default FilterType;



