/**
*
* @title tip
* Tooltip
* @description
*/

import React, { Component } from "react";
import Button from "bee-button";
import Tooltip from "bee-tooltip";
import Table from "../../src";

const columns = [
  {
    title: "", dataIndex: "a", key: "a", width: 80, className: "rowClassName",
    render: (text, record, index) => {
      return (
        <Tooltip inverse overlay={text}>
          <span tootip={text} style={{
            display: "inline-block",
            width: "60px",
            textOverflow: "ellipsis",
            overflow: "hidden",
            whiteSpace: "nowrap",
            verticalAlign: "middle",
          }}>{text}</span>
        </Tooltip>
      );
    }
  },
  { id: "123", title: "", dataIndex: "b", key: "b", width: '10%' },
  { title: "", dataIndex: "c", key: "c", width: 200 },
  {
    title: "",
    dataIndex: "d",
    key: "d",
    render(text, record, index) {
      return (
        <div style={{ position: 'relative' }} title={text} >
          <a
            href="javascript:;"
            tooltip={text}
            onClick={() => {
              alert('' + index + ':' + text);
            }}
          >
            
              </a>
        </div>
      );
    }
  }
];

const data = [
  { a: "", b: "", c: 41, d: "", key: "1" },
  { a: "", b: "", c: 67, d: "", key: "2" },
  { a: "", b: "", c: 25, d: "", key: "3" }
];

class Demo1 extends Component {

  constructor(props) {
    super(props);
    this.state = {
      data: data,
      selectedRowIndex: 0
    }
  }

  render() {
    return (
   
        <Table
          columns={columns}
          data={data}
          parentNodeId='parent'
          height={43}
          headerHeight={42}
          onRowClick={(record, index, indent) => {
            this.setState({
              selectedRowIndex: index
            });
          }}
        />

     
    );
  }
}

export default Demo1;



