import React, { Component } from 'react';
import Table from '../../src';
import multiSelect from "../../src/lib/multiSelect.js";
import Checkbox from 'bee-checkbox';

const columns12 = [
  {
    title: "",
    dataIndex: "a",
    key: "a",
    width: 100
  },
  {
    title: "",
    dataIndex: "b",
    key: "b",
    width: 100
  },
  {
    title: "",
    dataIndex: "c",
    key: "c",
    width: 200,
    sorter: (a, b) => a.c - b.c
  },
  {
    title: "",
    dataIndex: "d",
    key: "d"
  }
];

const data12 = [
  { a: "", b: "", c: 30,d:'', key: "2",_checked:true },
  { a: "", b: "", c: 41,d:'', key: "1" ,_checked:true},
  { a: "", b: "", c: 25,d:'', key: "3" ,_checked:true}
];
//tablerenderstate
let MultiSelectTable  = multiSelect(Table, Checkbox);

class Demo12 extends Component {
  constructor(props) {
    super(props);
    this.state = {
      data: data12
    };
  }
  getSelectedDataFunc = data => {
    console.log(data);
  };
  
  render() {
    let multiObj = {
      type: "checkbox"
    };
    return (
      <MultiSelectTable 
        columns={columns12} 
        data={data12} 
        multiSelect={multiObj}
        getSelectedDataFunc={this.getSelectedDataFunc}/>
    );
  }
}

export default Demo12;


