/**
*
* @title loading
* @description  loadingbooleanobjobjbee-loading
*
*/

import React, { Component } from "react";
import Table from "../../src";
import Button from "bee-button";

const columns17 = [
  { title: "", dataIndex: "a", key: "a", width: 100 },
  { id: "123", title: "", dataIndex: "b", key: "b", width: 100 },
  { title: "", dataIndex: "c", key: "c", width: 200 },
  {
    title: "",
    dataIndex: "d",
    key: "d",
    render(text, record, index) {
      return (
        <a
          href="#"
          onClick={() => {
            alert(''+index+':'+text);
          }}
        >
          
        </a>
      );
    }
  }
];

const data17 = [
  { a: "", b: "", c: 41, d: "", key: "1" },
  { a: "", b: "", c: 67, d: "", key: "2" },
  { a: "", b: "", c: 25, d: "", key: "3" }
];

class Demo17 extends Component {
  constructor(props){
    super(props);
    this.state = {
      loading : true
    }
  }
  changeLoading = () => {
    this.setState({
      loading : !this.state.loading
    })
  }
  render() {
    return (
      <div>
        <Button
          className="editable-add-btn"
          type="ghost"
          onClick={this.changeLoading}
        >
          loading
        </Button>
        <Table
          columns={columns17}
          data={data17}
          title={currentData => <div>: </div>}
          footer={currentData => <div>: </div>}
          // loading={this.state.loading}boolean
          loading={{show:this.state.loading,loadingType:"line"}}
        />
      </div>
    );
  }
}

export default Demo17;



